.row{
    .article1{
        margin-top: 30px;
        display: flex;
        font-weight: bold;
        font-size: 20px;
        
        .itemBox{
            width: 33.33%;
            height: 600px;
            background: #000;
            padding: 50px;
            transition: all 0.3s;
            &:hover{
                background: #ff0000;
                p:nth-of-type(1){color: #fff;}
            }
            img{width: 100%};
            p{ 
                margin-top: 10px;
              &:nth-of-type(1){
                  color: #ff0000;
                }
              &:nth-of-type(2){color: #fff;}
            }
        }
        .youTube{width: 66.66%;}
    }
    .article2{
        margin-top: 30px;
       width: 100%;
       height: 400px;
       display: flex;
       justify-content: space-between;
        .item001Box{
          flex:1;
            .fashionImg{  
                height:400px; 
                position: relative;
                img{
                    height: 400px;
                    transition: all 0.3s;
                }
                h2{
                    color: #fff;  
                    font-family: 'Abril Fatface', cursive;
                    font-size: 50px;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                }
                img:hover{border: 20px solid #ff0000;}
            }
          
        }
        .item002Box {
            flex:1;
