.headerOuter{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    border-bottom:2px solid #ddd ;
  
    .row{ 
        h1{
            padding: 10px 0;
            img{ width: 100%;}
        }
        .topMenu{
            padding-top:20px ;
            display: flex;
            justify-content: space-between;
            .sns {
                display: flex;
            li{
                margin-right:20px; font-size: 25px; cursor: pointer;
                &:hover{color: #ff0000;}
            }
            }
            .depth1 {
                display: flex;

                li.dp1{
                    margin-right:10px; font-size: 20px; font-weight: bold; cursor: pointer;
                    position: relative; padding-right: 10px;
                    &:after{content: '' ; width: 1px; height: 50px; background: #000;
                        position: absolute; top: -20px; right:0 ;}
                }
                li.dp1:nth-child(1){
                    position: relative;
                   
                    .depth2{
                        width: 150px;
                        display: none;
                        border: 1px solid #ddd;
                        background: #ddd;
                        margin-top:10px;
                        font-size: 16px;
                        position: absolute;
                        top: 100%; left: 0;
                        z-index: 99;
                        transition: all 0.5s;

                    li{padding: 10px;
                    &:hover{background: #ddd;}
                    }
                    &.opener{display: block; }
                    }
                
                }
                li.dp1:nth-child(2){
                    &:after{display: none;}
                }
            }
            
        }
        .mainMenu{
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 30px;
            margin-top: 30px;
            cursor: pointer;
            transition: all 0.5s;
            font-family: 'Abril Fatface', cursive;
            position: relative;
            .menuClick{
               
                &:hover{ color: #ff0000; }
            }
            .menuItem{
               
                display: none;
                .m1 {
                    margin-right: 30px;
                    transition: all 0.5;
                    &:hover{ color: #ff0000; }
                }
                .m1:nth-child(1){margin-left: 30px;}
                
                &.menuopen{display: block;}
            }

            .relOuter{
                display: flex;
                align-items: center;
                padding: 0px 20px; 
                background:none;
                .select{
                    font-size:50px;
                    &:hover{color: #ff0000;}
                };
                .retrieval {
                   display:none;
                   position: relative;
                  .retrievalBox{
                      display: flex;
                      align-items: center;
                    input{
                        height:40px; border:3px solid #000;  
                        border-radius:5px; vertical-align:middle;
                       margin-left: 10px;
                     }
                    button{
                        margin-left: 5px;
                        background: #ff0000;
                        border: 3px solid #000;
                        font-size: 18px;
                        color: #fff;
                        padding: 5px;
                        border-radius:5px;
                    }

                   }
                   &.open{
                    display: block;
                   }
                }
             }
        }
    }
}

